.details__img {
    position: relative;
    
    margin-bottom: 4rem;
}
@media screen and (min-height: 770px) {
    .details__img {
        margin-bottom: 8rem;
    }
}
.details__img img {
    display: block;
    margin: 50px auto;
    object-fit: cover;
    width: 100%;
    max-width: 259px;
    max-height: 259px;
    border-radius: 50%;
   
    
}
.active .details__img img{
    animation: round 20s linear infinite;
}
@keyframes round {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.details__title {
    color: white;
    font-size: 28px;
    text-align: center;
    margin-top: -20px;
}
.details__artist {
    color: lightgrey;
    font-size: 20px;
    text-align: center;
    margin-bottom: 20px;
}
